body {
    margin: 0;
    text-align: center;
    background-image: linear-gradient(90deg,#ff8a00,#e52e71);
}

.card {
    background: #eee;
    padding: 20px;
    margin: 30px;
    width: 300px;
    min-height: 250px;
    border-radius: 10px;
    box-shadow: 5px 5px 30px 7px #000;
    display: inline-block;
    overflow: hidden;
}
.card p{
    text-align: left;
}

.avatar{
    height: 100px;
    width:100;
    border-radius: 100%;
    box-shadow: 0 0 30px 7px black;
}

.sidePane{
    position: fixed !important;
}

.login{
    background-color: #dadabaa1;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    height: 300px;
    width: 500px;
    margin: 10% auto;
    box-shadow: 0 0 10px 10px #8e8e8e40;
}